import React, { useEffect } from "react";
import { motion, useAnimation } from "framer-motion";

import {
  Navigation,
  Pagination,
  Scrollbar,
  A11y,
  Autoplay,
} from "swiper/modules";
import { Swiper, SwiperSlide } from "swiper/react";
import { EffectCoverflow } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
import "swiper/css/autoplay";
import "swiper/css/effect-coverflow";

function Worldview() {
  const animationControls = useAnimation();

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      if (scrollPosition > 500) {
        animationControls.start({ opacity: 1, y: 0 });
      } else {
        animationControls.start({ opacity: 0, y: 600 });
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [animationControls]);

  const items = [
    {
      src: "./assets/back7.jpg",
      maintext: "",
      text: "     .      .",
    },
    {
      src: "./assets/back8.jpg",
      maintext: " ",
      text: "    ,     .      .",
    },
    {
      src: "./assets/back3.jpg",
      maintext: " ",
      text: "       ,             .",
    },
    {
      src: "./assets/back2.jpg",
      maintext: " ",
      text: "  ,      .     .",
    },
    {
      src: "./assets/back6.jpg",
      maintext: "",
      text: "      .       .",
    },
  ];

  return (
    <div className="worldview">
      <div className="worldview-blur"></div>
      <div className="worldview-box">
        <motion.div
          animate={animationControls}
          initial={{ opacity: 0 }}
          transition={{ ease: "easeInOut", duration: 0.4 }}
        >
          <div className="worldview-topname">  </div>
        </motion.div>

        <div className="worldview-swiperBox">
          <Swiper
            modules={[
              Navigation,
              Pagination,
              Scrollbar,
              A11y,
              Autoplay,
              EffectCoverflow,
            ]}
            rewind={true}
            spaceBetween={10}
            slidesPerView={3}
            effect="coverflow"
            autoplay={{
              delay: 5000,
            }}
            loop={true}
            coverflowEffect={{
              rotate: 30,
              stretch: 5,
              depth: 30,
              modifier: 1,
              slideShadows: false,
            }}
          >
            {items.map((item, idx) => {
              return (
                <SwiperSlide key={idx}>
                  {({ isNext }) => (
                    <motion.div
                      animate={animationControls}
                      initial={{ opacity: 0, y: 500 }}
                      transition={{ ease: "easeInOut", duration: 0.8 }}
                    >
                      <div>
                        <div
                          className={`worldview-img ${
                            isNext ? "active" : "not active"
                          }`}
                        >
                          <img src={item.src} alt="" />
                        </div>

                        <div className="worldview-maintext">
                          {item.maintext}
                        </div>
                        <div className="worldview-text">{item.text}</div>
                      </div>
                    </motion.div>
                  )}
                </SwiperSlide>
              );
            })}
          </Swiper>
        </div>
      </div>
    </div>
  );
}

export default Worldview;